/* Typography Base Styles */
body {
  color: #ffffff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

/* Base styles for all sections */
.section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  position: relative;
  overflow: hidden;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

.section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

/* Shared content styles */
.section-content {
  position: relative;
  z-index: 2;
  max-width: 1600px;
  width: 98%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 2.5rem;
  background: rgba(10, 10, 10, 0.92);
  backdrop-filter: blur(20px);
  border-radius: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Shared title styles */
.section-title {
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
  letter-spacing: -1px;
  line-height: 1.1;
  text-transform: uppercase;
  color: #ffffff;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.5),
               0 2px 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 3rem;
}

/* Shared stats container styles */
.stats-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.stats-card {
  padding: 2.5rem;
  border-radius: 25px;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  background: rgba(20, 20, 20, 0.95);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  text-align: center;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(255, 255, 255, 0.15);
}

.stats-card:hover {
  transform: translateY(-12px) scale(1.02);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.3);
}

.stats-card h3 {
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #ffffff;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  margin-bottom: 1.25rem;
}

.stats-card .number {
  font-size: 4rem;
  font-weight: 800;
  line-height: 1;
  margin-bottom: 0.5rem;
  text-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Twitch Section */
.twitch-section {
  background: linear-gradient(135deg, #0e0e10 0%, #9146ff 100%);
  position: relative;
  overflow: hidden;
}

.twitch-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at center, transparent 0%, rgba(0, 0, 0, 0.8) 100%);
  z-index: 1;
}

.twitch-content {
  position: relative;
  z-index: 2;
  max-width: 1600px;
  width: 98%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  padding: 2rem;
}

.twitch-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.twitch-section .stats-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  width: 100%;
  max-width: 1200px;
}

.twitch-section .stats-card {
  background: rgba(20, 20, 20, 0.9);
  padding: 2rem;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(145, 70, 255, 0.2);
  box-shadow: 0 8px 32px rgba(145, 70, 255, 0.1);
}

.twitch-section .stats-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, #9146ff, transparent);
}

.twitch-section .stats-card .number {
  font-size: 3.5rem;
  font-weight: 800;
  background: linear-gradient(45deg, #ffffff 30%, #9146ff 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: none;
  filter: drop-shadow(0 2px 4px rgba(145, 70, 255, 0.3));
}

.twitch-section .section-title {
  font-size: 3.5rem;
  font-weight: 800;
  margin: 0;
  background: linear-gradient(45deg, #ffffff, #9146ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0 30px rgba(145, 70, 255, 0.3);
  display: flex;
  align-items: center;
  gap: 1rem;
}

.twitch-section .platform-icon {
  font-size: 4rem;
  color: #9146ff;
  filter: drop-shadow(0 0 10px rgba(145, 70, 255, 0.5));
}

