}

/* Responsive Design */
@media (max-width: 1200px) {
  .section-content {
    width: 95%;
    padding: 2rem;
  }
  
  .stats-container {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }

  .section-title {
    font-size: 3.5rem;
  }

  .video-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .section {
    padding: 1.5rem;
  }

  .section-content {
    padding: 1.5rem;
    gap: 2rem;
  }

  .section-title {
    font-size: 2.75rem;
    text-shadow: 0 3px 6px rgba(0, 0, 0, 0.4);
  }

  .platform-icon {
    font-size: 3.5rem;
  }

  .stats-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .stats-card {
    padding: 2rem;
  }

  .stats-card .number {
    font-size: 3rem;
  }

  .video-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2.25rem;
  }

  .stats-card .number {
    font-size: 2.75rem;
  }

  .platform-icon {
    font-size: 3rem;
  }

  .video-info h4 {
    font-size: 1rem;
  }

  .video-info p {
    font-size: 0.9rem;
  }
}
